.header__link{
    text-decoration: none;
    color: #FFF;
    text-align: right;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    transition: opacity 0.3s linear;
}

.header__link:hover{
    opacity: 0.6;
}